import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { FacilityComponent } from './facility.component';
import { AddFacilitySlotComponent } from './components/add-facility-slot/add-facility-slot.component';
import { FacilityReportsComponent } from './components/facility-reports/facility-reports.component';
import { FacilitySetupComponent } from './components/facility-setup/facility-setup.component';
import { FacilityCreateBookingComponent } from './components/facility-create-booking/facility-create-booking.component';
import { FacilityBookingActionsComponent } from './components/facility-booking-actions/facility-booking-actions.component';
import { FullCalendarComponent } from '../../shared/components/full-calendar/full-calendar.component';

const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: FacilitySetupComponent},
	{ path: 'create-booking', component: FacilityCreateBookingComponent},
	{ path: 'edit-booking/:id', component: FacilityCreateBookingComponent},
	{ path: 'calendar', component: FullCalendarComponent},
	{ path: 'booking-actions', component: FacilityBookingActionsComponent},
	{ path: 'reports', component: FacilityReportsComponent },
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FacilityRoutingModule { }
