import { Component, OnInit, Injector, HostListener } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { UserService } from '../../../../api/services/user.service';
import { FacilityService } from '../../../../api/services/facility.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';


import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-facility-booking-actions',
  templateUrl: './facility-booking-actions.component.html',
  styleUrls: ['./facility-booking-actions.component.scss']
})
export class FacilityBookingActionsComponent implements OnInit {
  
  bookingListData:any;

  isBookingDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 20;

  booking:any;
  bookingData:string = "";

  modalService:any;

  staffData:string = "";

  facilityCategoryData:any;
  facilitySlotData:any;
  statusTypeData:any;

  userListData:any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private injector: Injector,
  	private userService:UserService,
    private facilityService:FacilityService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
  	this.modalService = this.injector.get(ModalService);
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'desc' : 'asc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  getFacilityBookingName(id){
  	
  	var data = _.filter( this.facilityCategoryData, function( item ) {
      if(item.apartmentFacilityId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].facilityName;
    }

  }

  getFacilityBookingSlot(id){
  	
  	var data = _.filter( this.facilitySlotData, function( item ) {
      if(item.apartmentFacilitySlotId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
     
     var beginTime = moment(data[0].slotBeginTime, 'HH:mm:ss').toDate();
     var slotBeginTime = moment(beginTime).format('HH:mm A');

     var EndTime = moment(data[0].slotEndTime, 'HH:mm:ss').toDate();
     var slotEndTime = moment(EndTime).format('HH:mm A');

     return slotBeginTime+" - "+slotEndTime;


    }

  }

  getBookedDate(date){
  	return moment(date).format("DD/MM/YYYY");
  }

  getUserName(id){
  	
  	var data = _.filter( this.userListData, function( item ) {
      if(item.userId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].firstName;
    }

  }

  getBookingStatus(id){

    var data = _.filter( this.statusTypeData, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].lookupValueName.toLowerCase();
    }

  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  ngOnInit() {

  	this.facilityService.getApartmentFacilitiesByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.facilityCategoryData = res.filter(item => {
        return item.isActive;
      })

      console.log(this.facilityCategoryData);
      
    },
    error => {

    });

    //status 
    this.lookupService.getLookupValueByLookupTypeId(40).subscribe((res:any) => {
          
      this.statusTypeData = res.filter( item => {
          return item.isActive;
      });

    },
    error => {
      
    }
    );

    this.facilityService.getApartmentFacilitySlotsByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.facilitySlotData = res.filter(item => {
        return item.isActive;
      })

      

    },
    error => {

    });

    this.userService.getAllUsersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

	    this.userListData = res.filter(data => {
	      return data.isActive
	    });

	    

    },
    error => {
      
    });

  	this.facilityService.getApartmentFacilityBookingsByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

  		//filter active true items
	    this.bookingListData = res.filter(data => {
	      return data.isActive
	    });

	    

	    this.totalItems = this.bookingListData.length;

	    if(this.totalItems>this.itemLimit){
	      this.ItemEndIndex = this.itemLimit;
	    }

	    else {
	      this.ItemEndIndex = this.totalItems;
	    }

	    this.isBookingDataLoaded = true;

  	},
    error => {
      
    });


    // delete booking
    this.sharedService.unitlistdeleteindexcast.subscribe( index => {

      if(index != null){

        var params = {
          apartmentFacilityBookingId: this.bookingListData[index].apartmentFacilityBookingId,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }

        this.isBookingDataLoaded = false;

        this.facilityService.deleteApartmentFacilityBooking(params).subscribe((res:any) => {

          this.bookingListData.splice(index, 1);
          this.totalItems = this.bookingListData.length;
          this.isBookingDataLoaded = true;
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    });


  }

}
