import { Component, OnInit, Input, Injector, Output, EventEmitter } from '@angular/core';

import { MatDialog } from '@angular/material';
import { MatDialogRef } from "@angular/material";

import { FacilityService } from '../../../../api/services/facility.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as moment from 'moment';
import * as _ from 'underscore';

@Component({
  selector: 'app-add-facility-slot',
  templateUrl: './add-facility-slot.component.html',
  styleUrls: ['./add-facility-slot.component.scss']
})
export class AddFacilitySlotComponent implements OnInit {
 
  @Input() slot:any;
  //@Input() slotCost:any;
  @Input() facilityId:any;
  @Input() type:any;

  alertMessage:string = "";
  isError:boolean = false;

  isSlotSubmitted:boolean = true;

  slotTimeDiff:any;

  minEndTime:any;

  modalService:any;

  @Output() facilityParams = new EventEmitter();

  constructor(
    private injector: Injector,
    public dialog: MatDialog,
  	private facilityService:FacilityService,
  	private sharedService:SharedService,
  	private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }


  submitAddFacilitySlotForm(form){

  	this.isSlotSubmitted = false;


  	if(this.type == 'new') {

  		let details = {

		  "apartmentId": parseInt(this.cookieService.get('apartmentId')),
		  "apartmentFacilityId": this.slot.apartmentFacilityId,
		  "slotBeginTime": moment(this.slot.slotBeginTime).format('HH:mm:ss'),
		  "slotEndTime": moment(this.slot.slotEndTime).format('HH:mm:ss'),
		  "cost": this.slot.cost,
		  "comment": this.slot.comment,
		  "isActive": true,
		  "insertedBy": parseInt(this.cookieService.get('userId')),
		  "insertedOn": "2019-12-16T12:34:36.708Z",
		  "updatedBy": 0,
		  "updatedOn": "2019-12-16T12:34:36.709Z"

		}

		 this.facilityService.addApartmentFacilitySlot(details).subscribe((res:any) => {

		 	if(res.message) {

		 		this.facilityParams.emit(true);
		 		this.isSlotSubmitted = true;
		 		this.sharedService.setAlertMessage("Slot added successfully");
		 	}

	      },
	      error => {

	      	this.isError = true;
          	this.alertMessage = "Some error occured";
	          
	      });

  		}

  		else {

  			let details = {

  			"apartmentFacilitySlotId": this.slot.apartmentFacilitySlotId,
			  "apartmentId": parseInt(this.cookieService.get('apartmentId')),
			  "apartmentFacilityId": this.slot.apartmentFacilityId,
			  "slotBeginTime": moment(this.slot.slotBeginTime).format('HH:mm:ss'),
			  "slotEndTime": moment(this.slot.slotEndTime).format('HH:mm:ss'),
			  "cost": this.slot.cost,
			  "comment": this.slot.comment,
			  "isActive": true,
			  "insertedBy": 0,
			  "insertedOn": "2019-12-16T12:34:36.708Z",
			  "updatedBy": parseInt(this.cookieService.get('userId')),
			  "updatedOn": "2019-12-16T12:34:36.709Z"

			}

			this.facilityService.updateApartmentFacilitySlot(details).subscribe((res:any) => {

			 	if(res.message) {
			 		this.facilityParams.emit(true);
			 		this.isSlotSubmitted = true;
			 		this.sharedService.setAlertMessage("Slot updated successfully");
			 	}

		      },
		      error => {

		      	this.isError = true;
	          	this.alertMessage = "Some error occured";
		          
		    });

  		}

  }

  emitSlotTime(){

  	this.minEndTime = moment(this.slot.slotBeginTime, 'HH:mm:ss').toDate();

  	var begin = moment(this.slot.slotBeginTime);
  	var end = moment(this.slot.slotEndTime);
  	//this.slotTimeDiff = Math.abs(begin.diff(end, 'hours'));

  	//this.slot.cost = this.slotCost*this.slotTimeDiff;

  }

  showConfirmModal(index) {
    this.modalService.showConfirmModal(index);
  }

  ngOnInit() {


  	if(this.type != 'new'){
  		this.slot.slotBeginTime = moment(this.slot.slotBeginTime, 'HH:mm:ss').toDate();
  		this.slot.slotEndTime = moment(this.slot.slotEndTime, 'HH:mm:ss').toDate();

  		//this.slot.cost = this.slotCost;
  	}
  	else {
  		this.slot.comment = "";
  	}

  	// delete slot
    this.sharedService.unitlistdeleteindexcast.subscribe( slot => {

      if(slot != null){

        var params = {
          apartmentFacilityBookingId: slot.apartmentFacilitySlotId,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }


        this.facilityService.deleteApartmentFacilitySlot(params).subscribe((res:any) => {

          this.facilityParams.emit(true);
          this.sharedService.setUnitListDeleteIndex(null);

        }, error => {
          console.log(error);
        });

        
      }

    });

  }

}
